<template>
	<view>
		<!-- 顶部自定义导航 -->
		<u-navbar :is-back="true"  title="订单详情" :height="height" :background="background" title-color="#ffffff" back-icon-color="#ffffff" >
		</u-navbar>
		<!-- map -->
		<map :latitude="latitude" :longitude="longitude" :markers="covers"></map>
		<!-- orderDetail -->
		<view class="orderDetail">
			<view class=" iconfont icongerenzhongxin-zhong">
				<span style="padding-left: 25upx;">客户订单信息</span>
			</view>
			<view class="parameter">
				<view class=" iconfont icontongxunlu">
					<span style="padding-left: 25upx;">{{data.name}}</span>
				</view>
				<view class="r-txt">
					<span style="padding-left: 25upx;">服务类型：{{data.type}}</span>
				</view>
			</view>
			<view class="parameter">
				<view class=" iconfont iconshouji">
					<span style="padding-left: 25upx;">{{data.tel}}</span>
				</view>
				<view class="r-txt ">
					<span style="padding-left: 25upx;">参考价格：{{data.budget}}元</span>
				</view>
			</view>
			<view class="parameter">
				<view class=" r-txt">
					<span>服务时长：{{data.duration}}</span>
				</view>
				<view class="r-txt ">
					<span style="padding-left: 25upx;">上门时间：{{data.door_time}}</span>
				</view>
			</view>
			<view class="parameter">位置：{{data.origin}}</view>
			<view class="parameter">工具要求：{{data.label}}</view>
			<view class="parameter">备注：{{data.content}}</view>
			<button type="default" class="theme">导航目标</button>
			<!-- <image src="../../static/logo.png" mode=""></image> -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				height:"",
				background:{
					backgroundImage:"linear-gradient(90deg, #00ABEB, #54C3F1)",
				},
				data:{},
				id: 0, // 使用 marker点击事件 需要填写id
				title: 'map',
				latitude: 29.993299,
				longitude: 104.154709,
				covers: [
					{
					id: 0, 
					latitude: 29.994521,
					longitude: 104.154741,
					iconPath: '../../static/wait-list/location.png',
					width:20,
					height:20,
					content:'客户地址',//文本
				}, 
				]
			}
		},
		onLoad(event) {
			// TODO 后面把参数名替换成 payload
			const payload = event.detailDate || event.payload;
			// 目前在某些平台参数会被主动 decode，暂时这样处理。
			try {
				this.data= JSON.parse(decodeURIComponent(payload));
				console.log(this.data);
				let loction = this.data.longitude.split(",");
				console.log(loction);
				this.latitude = loction[1];
				this.longitude  =loction[0];
				this.covers[0].latitude = loction[1];
				this.covers[0].longitude  =loction[0];
			} catch (error) {
				this.data = JSON.parse(payload);
				console.log(this.data);
			}
		},
		methods: {

		}
	}
</script>

<style scoped>
	/* map */
	uni-map {
		position: relative;
		width: 100%;
		height: 722upx;
		display: block;
	}
	/* 修改小程序中map默认样式 */
	map{
		position: relative;
		width: 100%;
		height: 646upx;
		display: block;
	}
	label{
		display: inline-block !important;
	}
	/* orderDetail */
	.orderDetail{
		padding-left: 25upx;
		padding-right: 25upx;
		padding-top: 30upx;
		position: relative;
	}
	.parameter2{
		
	}
	.parameter .r-txt{
		width: 40%;
		text-align: left;
	}
	.parameter{
		display: flex;
		padding-top: 6upx;
		padding-bottom: 6upx;
		justify-content: space-between;
		font-size: 24upx;
		margin-top: 25upx;
	}
	.parameter .iconfont {
		font-size: 24upx;
	}
	.parameter .iconfont span{
		font-size: 24upx;
		text-align: left;
	}
	.parameter  span{
		text-align: left;
	}
	.orderDetail image{
		width: 130upx;
		height: 130upx;
		border-radius: 100%;
		border: 4upx solid #00ABEB;
		background-color: #CCCCCC;
		position: absolute;
		right: 80upx;
		top: -68upx;
	}
	.orderDetail button{
		background-color:#F86032 ;
		color: #FFFFFF;
		border-radius: 8upx;
		width: 482upx;
		height:76upx ;
		font-size: 40upx;
		line-height: 76upx;
		margin:40upx auto;
	}
	/* 修改小程序的默认button样式 */
	button{
		-webkit-appearance:none
	}
</style>
